.alert {
    .bi {
        width: 1rem;
        height: 1rem;
    }
}


.alert-left {
    border: 0;
    border-left: $alert-borer-width $alert-borer-type;
    @each $state, $value in $theme-colors {
        &.alert-#{$state} {
            border-color:  var(--bs-#{$state});
        }
    }
}
.alert-top {
    border: 0;
    border-top: $alert-borer-width $alert-borer-type;
