.swiper-pagination-bullets-dynamic .swiper-pagination-bullet-active-prev,
.swiper-pagination-bullets-dynamic .swiper-pagination-bullet-active-prev-prev,
.swiper-pagination-bullets-dynamic .swiper-pagination-bullet,
.swiper-pagination-bullets-dynamic .swiper-pagination-bullet-active-next,
.swiper-pagination-bullets-dynamic .swiper-pagination-bullet-active-next-next {
  transform: scale(1);
}

  // swiper arrow button
  .swiper-button-next,
  .swiper-button-prev {
    background-color: var(--#{$variable-prefix}primary);
    color: $white;
    height: 40px;
    width: 40px;

    &:after {
      font-size: 12px;
    }
  }

  .testimonial-section {

    .swiper-button-next,
    .swiper-button-prev {
      background-color: var(--#{$variable-prefix}secondary);
    }
  }

  .arrow-joint {
    width: 110px;
    height: 45px;

    .swiper-button-next {
      right: 0;
    }

    .swiper-button-prev {
      left: 0;
    }
  }

  .bg-primary,
  .bg-secondary {

    .swiper-button-next,
    .swiper-button-prev {
      background-color: $white;
      color: var(--#{$variable-prefix}primary);
    }
  }

  
  // swiper pagination
  .swiper-pagination-bullet {
    height: 10px;
    width: 10px;
    border-radius: 0;
    background: transparent;
    border: 1px solid var(--#{$variable-prefix}primary);
    opacity: 1;
  }

  .swiper-pagination-bullet-active {
    background: var(--#{$variable-prefix}primary);
  }

  .bg-primary,
  .bg-secondary {
    .swiper-pagination-bullet {
      border-color: $white;
    }

    .swiper-pagination-bullet-active {
      background: $white;
    }
  }

  #testimonial-one-slider{
    position: relative;
    .slider-circle-btn{
        .swiper-button{
            width: 30px;
            height: 30px;
            position: absolute;
            &.swiper-button-next {
                right: 0.2rem;
                left:auto;
                top: 45%;
                background: var(--#{$variable-prefix}primary);
                border-radius: 5rem;
                &:after {
                    content: 'next';
                    font-size:  0.75rem;;
                    color: $white;
                }
            }
            &.swiper-button-prev {
                &:after {
                    content: 'prev';
                    font-size:  0.75rem;;
                    color: $white;
                }
                right:auto;
                left: 0.2rem;
                top: 45%;
                background: var(--#{$variable-prefix}primary);
                border-radius: 5rem;
            }
        }
    }
}